<template>
    <div>
      <div class="index">
        <div class="header">
          <span>
            <img src="../assets/img/saoyisao.png">
          </span>
          <div class="searchBar">
            <input type="text" placeholder="请输入您想搜索的内容" v-model="search">
            <img src="../assets/img/searchIcon.png">
          </div>
          <span>
            <img src="../assets/img/news.png" @click="test">
          </span>
        </div>
        <mt-swipe class="swipe">
          <mt-swipe-item v-for="(slide, index) in slides" :key="index" class="slideImg">
            <img :src="slide.imgUrl">
          </mt-swipe-item>
        </mt-swipe>
        <div class="mainMenu">
          <ul class="menu">
            <li v-for="(menulist, index) in menulists" :key="index" @click="showMenu(index)">
              <span class="menulistImg">
                <img :src="menulist.img">
              </span>
              <span>{{ menulist.title}}</span>
            </li>
          </ul>
        </div>
        <div class="videoSection">
          <div class="title">热门视频</div>
          <div class="scrollArea">
            <ul class="videoList">
              <li v-for="(video, index) in videos" :key="index">
                <span class="videoCover" @click="play(index)">
                  <img :src="video.fVideoCoverImgUrl">
                  <div class="symbol" v-if="video.fIsOpen === '1'">{{ video.fVideoPrice }}元</div>
                </span>
                <span class="videoInfo">
                  <span class="pay">{{ video.fIsOpen === '1' ? '收费' : '免费'}}</span>
                  <span class="views">
                    <span class="viewIcon">
                      <img src="../assets/img/view.png">
                    </span>
                    <span class="viewCount">{{ video.fVideoLogCnt }}次</span>
                  </span>
                </span>
              </li>
            </ul>
          </div>
        </div>
        <div class="coachSection">
          <div class="title">明星教练</div>
          <div class="scrollArea">
            <ul class="coaches">
              <li v-for="(coach, index) in coaches" :key="index" @click="showCoach(index)">
                <span class="coachHead">
                  <img :src="coach.fPhoto">
                </span>
                <span class="coachName">{{ coach.fName }}</span>
              </li>
            </ul>
          </div>
        </div>
        <div class="gymSection">
          <div class="title">明星武馆</div>
          <div class="gyms">
            <ul class="gymList">
              <li v-for="(gym, index) in gyms" :key="index" @click="showGym(index)">
                <span class="gymHead">
                  <img :src="gym.fPhoto">
                </span>
                <span class="gymInfors">
                  <span class="gymInfo">
                    <span class="gymName">{{ gym.fName }}</span>
                    <star class="stars" :score="gym.fAvgStar"></star>
                  </span>
                  <span class="gymDetail">{{ gym.fIntroduce }}</span>
                </span>
              </li>
            </ul>
          </div>
        </div>
        <div class="borderSpace"></div>
        <div class="newsSection">
          <div class="newsTitle">
            <ul class="newsHead">
              <li :class="{active: news.isActive}" v-for="(news, index) in newsInfo" :key="index" @click="tabNews(index)">{{ news.info }}</li>
            </ul>
          </div>
          <div class="news">
            <ul class="newsList" v-if="showHengha">
              <li v-for="(article, index) in articles" :key="index" @click="showInfo(index)">
                <span class="articleImg">
                  <img :src="article.fArticleCover">
                </span>
                <span class="articleInfo">
                  <span class="articleTitle">{{ article.fArticleTitle }}</span>
                  <span class="info">
                    <span class="views">
                      <span class="viewIcon">
                        <img src="../assets/img/view.png">
                      </span>
                      <span class="viewCount">{{ article.fReadCnt }}浏览</span>
                    </span>
                    <span class="comments">
                      <span class="viewIcon">
                        <img src="../assets/img/comment.png">
                      </span>
                      <span class="viewCount">{{ article.fPraiseCnt}}评论</span>
                    </span>
                  </span>
                </span>
              </li>
            </ul>
            <ul class="hotNewsList" v-if="!showHengha">
              <li v-for="(hotNews, index) in hotNewsList" :key="index">
                <span class="articleImg">
                  <img :src="hotNews.fArticleCover">
                </span>
                <span class="articleInfo">
                  <span class="articleTitle">{{ hotNews.fArticleTitle }}</span>
                  <span class="info">
                    <span class="views">
                      <span class="viewIcon">
                        <img src="../assets/img/view.png">
                      </span>
                      <span class="viewCount">{{ hotNews.fReadCnt }}浏览</span>
                    </span>
                    <span class="comments">
                      <span class="viewIcon">
                        <img src="../assets/img/comment.png">
                      </span>
                      <span class="viewCount">{{ hotNews.fPraiseCnt}}评论</span>
                    </span>
                  </span>
                </span>
              </li>
            </ul>
          </div>
        </div>
        <button class="toTop" @click="toTop" v-if="isBottom">回顶部</button>
        <div style="height: 2rem;"></div>
      </div>
      <BottomNav></BottomNav>
    </div>
</template>

<script>
import { getHotVideos, getStarCoach, getStarGyms, getHotNews, checkVideo, payForVideo } from '../api/getdata'
import store from '../vuex/store'
import star from '../components/Star'
import { Swipe, SwipeItem, MessageBox, Toast } from 'mint-ui'
import BottomNav from '../components/BottomNav'
export default {
  name: 'Index',
  data () {
    return {
      showHengha: true,
      isBottom: false,
      search: '',
      slides: [{
        imgUrl: require('../assets/img/banner.gif')
      }, {
        imgUrl: require('../assets/img/banner1.jpeg')
      }, {
        imgUrl: require('../assets/img/banner2.jpeg')
      }, {
        imgUrl: require('../assets/img/banner3.png')
      }],
      menulists: [{
        img: require('../assets/img/culture.png'), title: '文化', link: 'culture'
      }, {
        img: require('../assets/img/health.png'), title: '养生', link: 'culture'
      }, {
        img: require('../assets/img/teachers.png'), title: '名师堂', link: 'famousTeacher'
      }, {
        img: require('../assets/img/compitition.png'), title: '赛事'
      }, {
        img: require('../assets/img/shopping.png'), title: '商城'
      }],
      videos: [],
      coaches: [],
      gyms: [],
      newsInfo: [{
        info: '哼哈', isActive: true
      }, {
        info: '头条', isActive: false
      }],
      articles: [],
      hotNewsList: []
    }
  },
  methods: {
    toTop () {
      window.scrollTo(0, 0)
    },
    showMenu (index) {
      store.commit('saveArticleType', this.menulists[index].title)
      this.$router.push(`/home/index/${this.menulists[index].link}`)
    },
    bottom () {
      var clientHeight = document.documentElement.clientHeight
      var scrollTop = document.documentElement.scrollTop || document.body.scrollTop
      var scrollHeight = document.documentElement.scrollHeight
      if (clientHeight + scrollTop >= scrollHeight) {
        this.isBottom = true
      } else {
        this.isBottom = false
      }
    },
    tabNews (index) {
      /* for (var i = 0; i < this.newsInfo.length; i++) {
        this.newsInfo[i].isActive = false
        this.newsInfo[index].isActive = true
      }
      this.showHengha = !this.showHengha */
      console.log(index)
    },
    getVideos () {
      let hotVideo = getHotVideos({pageIndex: 1, pageSize: 6})
      hotVideo.then(res => {
        console.log(res.data.result)
        this.videos = res.data.result
      })
    },
    getCoaches () {
      let starCoaches = getStarCoach({pageIndex: 1, pageSize: 10})
      starCoaches.then(res => {
        // console.log(res.data)
        this.coaches = res.data
      })
    },
    getGyms () {
      let starGyms = getStarGyms({pageIndex: 1, pageSize: 3})
      starGyms.then(res => {
        // console.log(res.data)
        this.gyms = res.data.result
      })
    },
    play (index) {
      if (this.videos[index].fIsOpen === '0') {
        store.commit('getVideoInfo', this.videos[index])
        this.$router.push('/home/mine/videoPlayer')
      } else {
        let data = checkVideo({videoId: this.videos[index].fVideoId})
        data.then(res => {
          console.log(res)
          if (res.status === '-1') {
            MessageBox.confirm('', {
              message: '确认付费观看?'
            }).then(action => {
              if (action === 'confirm') {
                let pay = payForVideo({videoId: this.videos[index].fVideoId})
                pay.then(res => {
                  console.log(res)
                  if (res.status === '0') {
                    Toast({
                      message: '购买视频成功',
                      duration: 1500
                    })
                    store.commit('getVideoInfo', this.videos[index])
                    this.$router.push('/home/mine/videoPlayer')
                  } else {
                    Toast({
                      message: '购买视频失败,请稍后再试',
                      duration: 1500
                    })
                  }
                })
              }
            }).catch(err => {
              if (err === 'cancel') {
                Toast({
                  message: '已取消',
                  duration: 1500
                })
              }
            })
          } else {
            store.commit('getVideoInfo', this.videos[index])
            this.$router.push('/home/mine/videoPlayer')
          }
        })
      }
    },
    showCoach (index) {
      localStorage.setItem('fInstructorId', this.coaches[index].fInstructorId)
      localStorage.setItem('fUserId', this.coaches[index].fUserID)
      this.$router.push('/home/teachers/userInfor')
    },
    showGym (index) {
      localStorage.setItem('fClubId', this.gyms[index].fClubId)
      localStorage.setItem('fUserId', this.gyms[index].fUserID)
      this.$router.push('/home/teachers/gymInfo')
    },
    getHenghaNews () {
      let henghaNews = getHotNews({typeId: 1, pageIndex: 1, pageSize: 3})
      henghaNews.then(res => {
        console.log(res.data.result)
        this.articles = res.data.result
      })
    },
    getHotNews () {
      let hotNews = getHotNews({typeId: 2, pageIndex: 1, pageSize: 3})
      hotNews.then(res => {
        // console.log(res.data)
        this.hotNewsList = res.data.result
      })
    },
    showInfo (index) {
      store.commit('saveArticleId', this.articles[index].fArticleId)
      this.$router.push('/home/index/news')
    },
    test () {
      console.log('test')
    }
  },
  mounted () {
    this.getVideos()
    this.getCoaches()
    this.getGyms()
    this.getHenghaNews()
    this.getHotNews()
    window.addEventListener('scroll', this.bottom)
    /* var ws = new WebSocket('ws://47.102.101.78:8080/ws')
    ws.onopen = function () {
      ws.send('hello')
      console.log('建立远程连接成功')
      // console.log(ws.readyState)
    }
    ws.onclose = function () {
      console.log('断开连接')
    }
    ws.onmessage = function (e) {
      var data = e.data
      console.log(data)
    } */
  },
  beforeDestroy () {
    window.removeEventListener('scroll', this.bottom)
  },
  components: {
    BottomNav,
    star,
    'mint-swipe': Swipe,
    'mint-swipe-item': SwipeItem
  }
}
</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
  }

  .header {
    display: flex;
    width: 100%;
    height: 0.96rem;
    padding: 0.16rem 0;
    background: #fff;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    border-bottom: 1px solid #f5f5f5;
    z-index: 999;
  }

  .header span {
    width: 0.8rem;
    margin: 0 0.4rem;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .header span img {
    width: 100%;
    height: 100%;
  }

  .searchBar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 7.46667rem;
    height: 1rem;
    border: 1px solid #e6e6e6;
    border-radius: 0.5rem;
  }

  .searchBar input {
    outline: none;
    width: 6rem;
    margin-left: 0.4rem;
    border: none;
    outline: none;
    font-size: 0.42667rem;
  }

  .searchBar img {
    width: 0.48rem;
    height: 0.48rem;
    margin-right: 0.2rem;
  }

  .swipe {
    margin-top: 1.28rem;
    height: 4.8rem;
    z-index: -1;
  }

  .slideImg img {
    width: 100%;
    height: 100%;
  }

  .mainMenu {
    background: #ffffff;
    height: 3rem;
    border-radius: 50% / 10% 10% 0 0;
    margin-top: -0.42667rem;
    z-index: 99;
    border-bottom: 0.32rem solid #f5f5f5;
  }

  .menu {
    list-style: none;
    display: flex;
    width: calc(100% - 0.42667rem);
    margin: 0 auto;
    padding-top: 0.4rem;
  }

  .menu li {
    width: 1.46667rem;
    margin: 0.22667rem;
  }

  .menu li span {
    font-size: 0.42667rem;
    display: block;
    margin-top: 0.32rem;
  }

  .menulistImg {
    display: flex;
    width: 0.82667rem;
    height: 0.82667rem;
    margin: 0  auto;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
  }

  .menulistImg img{
    width: 100%;
    height: 100%;
  }

  .newsSection,
  .gymSection,
  .coachSection,
  .videoSection {
    width: calc(100% - 0.8rem);
    margin: 0 auto;
    margin-top: 0.66667rem;
  }

  .title {
    text-align: start;
    font-size: 0.42667rem;
    margin-left: 0.21333rem;
    position: relative;
    z-index: -1;
  }

  .title::before {
    content: '';
    display: block;
    position: absolute;
    width: 0.05333rem;
    height: 100%;
    left: -0.21333rem;
    top: 0;
    background: #ffd200;
  }

  .scrollArea {
    width: 100%;
    margin-top: 0.32rem;
    overflow-x: scroll;
  }

  .scrollArea::-webkit-scrollbar {
    display: none;
  }

  .coaches,
  .videoList {
    list-style: none;
    width: 300%;
  }

  .coaches li {
    float: left;
    width: 1.6rem;
    margin-left: 0.4rem;
  }

  .videoList li {
    width: 3.57333rem;
    float: left;
    margin-left: 0.29333rem;
  }

  .videoCover {
    display: block;
    width: 100%;
    height: 2.4rem;
    position: relative;
    overflow: hidden;
  }

  .coaches li:first-child,
  .videoList li:first-child {
    margin-left: 0;
  }

  .coaches li img,
  .videoList li img {
    width: 100%;
    height: 100%;
  }

  .videoInfo {
    display: flex;
    justify-content: space-between;
    margin-top: 0.16rem;
  }

  .comments,
  .views {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .comments {
    margin-left: 0.6rem;
  }

  .viewIcon {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 0.4rem;
    height: 0.3rem;
    margin-right: 0.10667rem;
  }

  .viewIcon img {
    width: 100%;
  }

  .viewCount {
    font-size: 0.34667rem;
    color: #666666;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 0.10667rem;
  }

  .pay {
    display: flex;
    width: 1rem;
    height: 0.53333rem;
    justify-content: center;
    align-items: center;
    border-radius: 50%/100%;
    font-size: 0.34667rem;
    color: #7394ff;
    background: #f5f5f5;
  }

  .coachHead {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    overflow: hidden;
    width: 1.6rem;
    height: 1.6rem;
  }

  .coachHead img {
    width: 100%;
    height: 100%;
  }

  .coachName {
    font-size: 0.4rem;
    color: #666666;
    display: block;
    margin-top: 0.32rem;
  }

  .gymList {
    list-style: none;
    margin-top: 0.26667rem;
  }

  .gymList li {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0.16rem 0;
  }

  .gymHead {
    display: flex;
    justify-content: center;
    align-items: center;
    flex:0 0 1.6rem;
    height: 1.6rem;
    border-radius: 50%;
    overflow: hidden;
  }

  .gymHead img {
    width: 100%;
  }

  .gymInfo {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .gymName {
    font-size: 0.4rem;
    color: #333333;
  }

  .gymInfors {
    margin-left: 0.4rem;
    overflow: hidden;
    font-size: 0.34667rem;
