    flex-grow: 1;
  }

  .gymDetail {
    display: inline-block;
    margin-top: 0.2rem;
    font-size: 0.34667rem;
    color: #333333;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
