  }

  .newsHead {
    display: flex;
    list-style: none;
  }

  .newsHead li {
    font-size: 0.37333rem;
    width: 1rem;
    height: 0.61333rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid #ffd200;
  }

  .newsHead li:first-child {
    border-top-left-radius: 0.2rem;
  }

  .newsHead li:last-child {
    border-bottom-right-radius: 0.2rem;
  }

  .active {
    background: #ffd200;
    color: #fff;
  }

  .news {
    margin-top: 0.32rem;
  }

  .hotNewsList li,
  .newsList li {
    display: flex;
    margin: 0.18667rem 0;
  }

  .articleImg {
    flex: 0 0 3.2rem;
    height: 2.24rem;
    border-radius: 0.2rem;
  }

  .articleImg img {
    width: 100%;
    height: 100%;
  }

  .articleInfo {
    display: inline-block;
    margin-left: 0.32rem;
    width: calc(100% - 3.54rem);
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .articleTitle {
    font-size: 0.37333rem;
    color: #333333;
    display: inline-block;
    margin-bottom: 0.4rem;
    width: 100%;
    text-align: start;
    white-space: nowrap;
  }

  .info {
    display: flex;
  }

  .toTop {
    display: flex;
    align-items: center;
    justify-content: center;
    color: #ffffff;
    font-size: 0.42667rem;
    background: #ffd200;
    width: 1.96667rem;
    height: 1.96667rem;
    border-radius: 50%;
    border: none;
    outline: none;
    position: fixed;
    right: 0.4rem;
    bottom: 1.5rem;
  }

  .symbol {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 2rem;
    height: 0.53333rem;
    background: #ffd200;
    font-size: 0.2rem;
    position: absolute;
    left: 2rem;
    top: 0.2rem;
    transform: rotate(45deg);
  }
</style>



